import service from '@/plugin/axios/service'

export function deleteStudent(id){
    return service({
        url:'/api/stu/'+id,
        method:'delete',
    })
}

export function updateStudent(student){
    return service({
        url:'/api/stu/',
        method:'put',
        data:student

    })
}

export function addStudent(student){
    return service({
        url:'api/stu/',
        method:'post',
        data:student
    })
}

export function getStudents() {
    return service({
        url: 'api/stu/all',
        method: 'get'
    })
}

export function getStudentByPage(clazz,page,size) {
    return service({
        url:'api/stu/page',
        method:'get',
        params:{class:clazz,page:page,size:size}
    })
    
}

export function searchStudentByName(name,page,size){
    return service({
        url:'/api/stu/search',
        params:{name:name,page:page,size:size}
    })
}